<template>
    <v-dialog v-model="popUpSelf" :max-width=width>
        <v-card class="pa-5 tw-flex-col tw-space-y-4">
          <div class="tw-text-xl tw-font-bold">{{title}}</div>
          <div class="tw-flex">
            <div class="pa-2">
              <v-icon color=" error" x-large>mdi-alert-circle-outline</v-icon>
            </div>
            <div class="pa-2">
              {{content}}
            </div>
          </div>

          <div>
            <v-btn text color="primary" @click="$emit('cancel')">å–æ¶ˆ</v-btn>
            <v-btn text color="primary" @click="$emit('confirm')">ç¡®å®š</v-btn>
          </div>

        </v-card>
    </v-dialog>
</template>

<script>
export default {
    model: {
        prop: "popUp",
        event: "change"
    },
    props: ["title","content","width", "popUp"],
    data() {
        return {
          popUpSelf: false
        }
    },
    watch: {
      popUp (val) {
        this.popUpSelf = val
      },
      popUpSelf() {
        this.$emit('change', this.popUpSelf)
      }
    }
}
</script>

